import request from 'superagent';

export function validate(values) {

  const errors = {};

  // validate name
  if (!values.name) {
    errors.name = "";
  } else if (values.name.length > 15) {
    errors.name = "15";
  }

  if (!values.email) {
    errors.email = '';
  } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
    errors.email = '';
  }

  if (!values.birthday) errors.birthday = '';

  if (!values.sex) errors.sex = '';

  if (!values.status) errors.status = '';

  return errors;
}

export function asyncValidate(values) {
  // send to server the values for validation

  // get the field name
  const name = arguments[3];

  if (['name', 'email'].indexOf(name) === -1) return Promise.resolve();

  const query = {};
  query[name] = values[name];

  const findEva = key => new Promise((resolve, reject) => {

    return request
      .get('/api/evaluations')
      .query(query)
      .end((err, res) => {
        if (res.statusCode !== 404) return resolve(key);

        return resolve();
      });
  });

  return findEva(name).then(key => {
    if (key === 'name') throw {name: ''};
    else if (key === 'email') throw {email: ''};
  });
}

export const asyncBlurFields = ['name', 'email'];



